import React, { useEffect, useRef, useState } from "react";
import "../styles/audio.css";
import cp from "../images/coldplay.jpeg";
import { Icon } from "semantic-ui-react";

function floorPad(n) {
  return Math.floor(n % 60)
    .toString()
    .padStart(2, "0");
}

let hasIntersected = false;

export default function AudioPlayer({ themeBtn }) {
  const audio = useRef(null);
  const timelineRef = useRef(null);
  const [playing, setPlaying] = useState(false);
  const [muted, setMuted] = useState(false);
  const [duration, setDuration] = useState(0);
  const [currentTime, setCurrentTime] = useState(0);

  useEffect(() => {
    audio.current = new Audio();
    audio.current.preload = "none";
    audio.current.autoplay = true;
    audio.current.src = "/ft/music.webm";
    audio.current.onloadeddata = () => {
      setDuration(audio.current.duration);
      setCurrentTime(audio.current.currentTime);
      audio.current.volume = 0.75;
    };
    setInterval(() => {
      if (!audio.current.paused) {
        setPlaying(true);
      }
      setCurrentTime(audio.current.currentTime);
    }, 500);
    const onInteraction = () => {
      if (audio.current.paused && !hasIntersected) {
        audio.current.play();
        setPlaying(true);
        hasIntersected = true;
      }
      document.addEventListener("visibilitychange", () => {
        if (document.hidden) {
          audio.current.pause();
          setPlaying(false);
        } else {
          audio.current.play();
          setPlaying(true);
        }
      });
      document.removeEventListener("click", onInteraction);
      document.removeEventListener("touchstart", onInteraction);
    };
    ["click", "touchstart"].forEach((e) => {
      document.addEventListener(e, onInteraction);
    });
    return () => {
      audio.current.pause();
      audio.current = null;
      document.removeEventListener("click", onInteraction);
      document.removeEventListener("touchstart", onInteraction);
    };
  }, []);

  function togglePlay() {
    if (audio.current.paused) {
      audio.current.play();
      setPlaying(true);
    } else {
      audio.current.pause();
      setPlaying(false);
    }
  }
  function handleTimeLineClick(e) {
    const timelineWidth = window.getComputedStyle(timelineRef.current).width;
    const timeToSeek = ((e.pageX - 50) / parseInt(timelineWidth)) * duration;
    if (!hasIntersected) return;
    audio.current.currentTime = timeToSeek;
  }
  function toggleVolume() {
    setMuted(!audio.current.muted);
    audio.current.muted = !audio.current.muted;
  }
  return (
    <div className="music">
      <img src={cp} alt="cold play" />
      <div className="audio-player">
        <div className="controls">
          <div
            className="play-container"
            role="button"
            tabIndex="0"
            onClick={togglePlay}
            onKeyDown={() => false}
          >
            <Icon name={playing ? "pause" : "play"} />
          </div>
          <div className="time">
            <div className="current">{`${floorPad(
              currentTime / 3600
            )}:${floorPad(currentTime / 60)}:${floorPad(currentTime)}`}</div>
            <div className="divider">/</div>
            <div className="length">01:16:48</div>
          </div>
          {(playing || hasIntersected) && (
            <div className="name">Best of ColdPlay</div>
          )}
          <div className="volume-container">
            <div
              className="volume-button"
              role="button"
              tabIndex="0"
              onClick={toggleVolume}
              onKeyDown={() => false}
            >
              <Icon
                className="volume"
                name={muted ? "volume off" : "volume up"}
                size="large"
              />
            </div>
          </div>
        </div>
        <div
          className="timeline"
          role="button"
          tabIndex="0"
          ref={timelineRef}
          onClick={handleTimeLineClick}
          onKeyDown={() => false}
        >
          <div
            className="progress"
            style={{ width: (currentTime / duration) * 100 + "%" }}
          ></div>
        </div>
      </div>
      {themeBtn}
    </div>
  );
}
